"use client";

import React, { useState, useEffect, useCallback } from "react";
import { useAuth } from "@/hooks/useAuth";
import { useRouter, useParams } from "next/navigation";
import { ChevronLeftIcon, DollarLineIcon, TimeIcon, CheckCircleIcon, AlertIcon, CopyIcon } from "@/icons";
import { formatCurrency } from "@/utils/format";
import { vendaFinalService, type VendaFinal } from "@/services/api/venda.service";
import { ThemeToggleButton } from "@/components/common/ThemeToggleButton";
import toast from "react-hot-toast";
import Image from "next/image";

export default function FaturaDetalhesPage() {
  const { user } = useAuth();
  const router = useRouter();
  const params = useParams();
  const [fatura, setFatura] = useState<VendaFinal | null>(null);
  const [loading, setLoading] = useState(true);
  const [copied, setCopied] = useState<string | null>(null);

  const loadFatura = useCallback(async () => {
    if (!params.id) return;
    try {
      setLoading(true);
      const faturaId = parseInt(params.id as string);
      
      const response = await vendaFinalService.getById(faturaId);
      setFatura(response.data);
    } catch (error) {
      console.error('Erro ao carregar fatura:', error);
      toast.error('Erro ao carregar dados da fatura');
    } finally {
      setLoading(false);
    }
  }, [params.id]);

  useEffect(() => {
    loadFatura();
  }, [loadFatura]);

  const copyToClipboard = async (text: string, type: string) => {
    try {
      await navigator.clipboard.writeText(text);
      setCopied(type);
      toast.success(`${type} copiado para a área de transferência!`);
      setTimeout(() => setCopied(null), 2000);
    } catch (error) {
      toast.error('Erro ao copiar para a área de transferência');
    }
  };

  const getStatusIcon = (status: string) => {
    switch (status) {
      case 'APPROVED':
        return <CheckCircleIcon className="w-5 h-5 text-green-500" />;
      case 'PENDING':
        return <TimeIcon className="w-5 h-5 text-yellow-500" />;
      case 'CANCELLED':
        return <AlertIcon className="w-5 h-5 text-red-500" />;
      default:
        return <TimeIcon className="w-5 h-5 text-gray-500" />;
    }
  };

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'APPROVED':
        return 'bg-green-100 text-green-800 dark:bg-green-900 dark:text-green-300';
      case 'PENDING':
        return 'bg-yellow-100 text-yellow-800 dark:bg-yellow-900 dark:text-yellow-300';
      case 'CANCELLED':
        return 'bg-red-100 text-red-800 dark:bg-red-900 dark:text-red-300';
      default:
        return 'bg-gray-100 text-gray-800 dark:bg-gray-900 dark:text-gray-300';
    }
  };
  const statusTranslate = (status: string) => {
    switch (status) {
      case 'APPROVED':
        return 'Pago';
      case 'PENDING':
        return 'Aguardando Pagamento';
      case 'CANCELLED':
        return 'Cancelado';
      default:
        return status;
    }
  };

  const getTypeIcon = (type: string) => {
    switch (type) {
      case 'PIX':
        return <DollarLineIcon className="w-6 h-6 text-blue-500" />;
      case 'BOLETO':
        return <DollarLineIcon className="w-6 h-6 text-green-500" />;
      default:
        return <DollarLineIcon className="w-6 h-6 text-gray-500" />;
    }
  };

  const formatDate = (dateString: string | Date) => {
    if (typeof dateString === 'string') {
      return new Date(dateString).toLocaleDateString('pt-BR');
    } else {
      return dateString.toLocaleDateString('pt-BR');
    }
  };

  const formatDateTime = (dateString: string | Date) => {
    if (typeof dateString === 'string') {
      return new Date(dateString).toLocaleString('pt-BR');
    } else {
      return dateString?.toLocaleString('pt-BR');
    }
  };

  const isOverdue = (vencimento: string | Date) => {
    if (typeof vencimento === 'string') {
      return new Date(vencimento) < new Date();
    } else {
      return vencimento < new Date();
    }
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center min-h-screen">
        <div className="animate-spin rounded-full h-32 w-32 border-b-2 border-blue-600"></div>
      </div>
    );
  }

  if (!fatura) {
    return (
      <div className="text-center py-12">
        <AlertIcon className="w-16 h-16 text-gray-400 mx-auto mb-4" />
        <h3 className="text-lg font-medium text-gray-900 dark:text-white mb-2">
          Fatura não encontrada
        </h3>
        <p className="text-gray-600 dark:text-gray-400 mb-4">
          A fatura solicitada não foi encontrada ou você não tem permissão para visualizá-la.
        </p>
        <button
          onClick={() => router.push('/dashboard/cliente/faturas')}
          className="px-4 py-2 bg-blue-600 hover:bg-blue-700 text-white rounded-lg transition-colors"
        >
          Voltar para Faturas
        </button>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      {/* Header */}
      <div className="bg-white dark:bg-gray-800 rounded-xl border border-gray-200 dark:border-gray-700 p-6">
        <div className="flex items-center justify-between">
          <div className="flex items-center space-x-4">
            <button
              onClick={() => router.back()}
              className="p-2 hover:bg-gray-100 dark:hover:bg-gray-700 rounded-lg transition-colors"
              title="Voltar"
            >
              <ChevronLeftIcon className="w-5 h-5 text-gray-600 dark:text-gray-400" />
            </button>
            <div>
              <h1 className="text-2xl font-bold text-gray-900 dark:text-white">
                Fatura #{fatura.id}
              </h1>
              <p className="text-gray-600 dark:text-gray-400 mt-1">
                Detalhes da fatura
              </p>
            </div>
          </div>
          <div className="flex items-center space-x-2">
            <ThemeToggleButton />
            {getStatusIcon(fatura.status)}
            <span className={`inline-flex items-center px-3 py-1 rounded-full text-sm font-medium ${getStatusColor(fatura.status)}`}>
              {fatura.status}
            </span>
          </div>
        </div>
      </div>

      <div className="grid grid-cols-1 lg:grid-cols-3 gap-6">
        {/* Informações da Fatura */}
        <div className="lg:col-span-2 space-y-6">
          {/* Dados Principais */}
          <div className="bg-white dark:bg-gray-800 rounded-xl border border-gray-200 dark:border-gray-700 p-6">
            <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-4">
              Informações da Fatura
            </h3>
            <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
              <div>
                <p className="text-sm text-gray-600 dark:text-gray-400">Valor:</p>
                <p className="text-2xl font-bold text-gray-900 dark:text-white">
                  {formatCurrency(fatura.valor)}
                </p>
              </div>
              <div>
                <p className="text-sm text-gray-600 dark:text-gray-400">Tipo de Pagamento:</p>
                <div className="flex items-center space-x-2">
                  {getTypeIcon(fatura.type)}
                  <p className="font-medium text-gray-900 dark:text-white">
                    {fatura.type}
                  </p>
                </div>
              </div>
              <div>
                <p className="text-sm text-gray-600 dark:text-gray-400">Data de Vencimento:</p>
                <p className={`font-medium ${
                  isOverdue(fatura.vencimento) && fatura.status === 'PENDING' 
                    ? 'text-red-600 dark:text-red-400' 
                    : 'text-gray-900 dark:text-white'
                }`}>
                  {formatDate(fatura.vencimento)}
                  {isOverdue(fatura.vencimento) && fatura.status === 'PENDING' && (
                    <span className="ml-2 text-red-600 dark:text-red-400 font-medium">
                      (Vencida)
                    </span>
                  )}
                </p>
              </div>
              <div>
                <p className="text-sm text-gray-600 dark:text-gray-400">Status:</p>
                <p className="font-medium text-gray-900 dark:text-white">
                  {statusTranslate(fatura.status)}
                </p>
              </div>
              {fatura.dataPagamento && (
                <div>
                  <p className="text-sm text-gray-600 dark:text-gray-400">Data de Pagamento:</p>
                  <p className="font-medium text-green-600 dark:text-green-400">
                    {formatDateTime(fatura.dataPagamento)}
                  </p>
                </div>
              )}
              <div>
                <p className="text-sm text-gray-600 dark:text-gray-400">Criada em:</p>
                <p className="font-medium text-gray-900 dark:text-white">
                  {formatDateTime(fatura.createdAt)}
                </p>
              </div>
            </div>
          </div>

          {/* Instruções de Pagamento */}
          {fatura.status === 'PENDING' && (
            <div className="bg-white dark:bg-gray-800 rounded-xl border border-gray-200 dark:border-gray-700 p-6">
              <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-4">
                Instruções de Pagamento
              </h3>
              
              {fatura.type === 'PIX' && (
                <div className="space-y-4">
                  {fatura.copiacola && (
                    <div>
                      <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
                        Código PIX (Copiar e Colar):
                      </label>
                      <div className="flex items-center space-x-2 p-3 bg-gray-50 dark:bg-gray-700 rounded-lg">
                        <input
                          type="text"
                          value={fatura.copiacola}
                          readOnly
                          className="flex-1 bg-transparent text-sm font-mono text-gray-700 dark:text-gray-300 border-none outline-none"
                          aria-label="Código PIX para copiar"
                        />
                        <button
                          onClick={() => copyToClipboard(fatura.copiacola!, 'Código PIX')}
                          className="p-2 hover:bg-gray-200 dark:hover:bg-gray-600 rounded transition-colors"
                          title="Copiar código PIX"
                        >
                          <CopyIcon className={`w-5 h-5 ${copied === 'Código PIX' ? 'text-green-500' : 'text-gray-500'}`} />
                        </button>
                      </div>
                    </div>
                  )}
                  
                  {fatura.qrcode && (
                    <div>
                      <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
                        QR Code PIX:
                      </label>
                      <div className="flex justify-center p-4 bg-gray-50 dark:bg-gray-700 rounded-lg">
                        <Image
                          src={fatura.qrcode.startsWith('data:image') ? fatura.qrcode : `data:image/png;base64,${fatura.qrcode}`} 
                          alt="QR Code PIX" 
                          width={100}
                          height={100}
                          className="max-w-48 max-h-48"
                        />
                      </div>
                    </div>
                  )}
                </div>
              )}
              
              {fatura.type === 'BOLETO' && fatura.codigobarra && (
                <div>
                  <label className="block text-sm font-medium text-gray-700 dark:text-gray-300 mb-2">
                    Código de Barras:
                  </label>
                  <div className="flex items-center space-x-2 p-3 bg-gray-50 dark:bg-gray-700 rounded-lg">
                    <input
                      type="text"
                      value={fatura.codigobarra}
                      readOnly
                      className="flex-1 bg-transparent text-sm font-mono text-gray-700 dark:text-gray-300 border-none outline-none"
                      aria-label="Código de barras para copiar"
                    />
                    <button
                      onClick={() => copyToClipboard(fatura.codigobarra!, 'Código de Barras')}
                      className="p-2 hover:bg-gray-200 dark:hover:bg-gray-600 rounded transition-colors"
                      title="Copiar código de barras"
                    >
                      <CopyIcon className={`w-5 h-5 ${copied === 'Código de Barras' ? 'text-green-500' : 'text-gray-500'}`} />
                    </button>
                  </div>
                </div>
              )}
              
              <div className="mt-4 p-4 bg-blue-50 dark:bg-blue-900/20 rounded-lg">
                <h4 className="font-medium text-blue-900 dark:text-blue-300 mb-2">
                  Instruções:
                </h4>
                <ul className="text-sm text-blue-800 dark:text-blue-400 space-y-1">
                  {fatura.type === 'PIX' ? (
                    <>
                      <li>• Copie o código PIX ou escaneie o QR Code</li>
                      <li>• Abra o aplicativo do seu banco</li>
                      <li>• Selecione a opção PIX</li>
                      <li>• Cole o código ou escaneie o QR Code</li>
                      <li>• Confirme o pagamento</li>
                    </>
                  ) : (
                    <>
                      <li>• Copie o código de barras</li>
                      <li>• Acesse o site ou aplicativo do seu banco</li>
                      <li>{'• Selecione a opção "Pagar Boleto"'}</li>
                      <li>• Cole o código de barras</li>
                      <li>• Confirme os dados e efetue o pagamento</li>
                    </>
                  )}
                </ul>
              </div>
            </div>
          )}

          {/* Status Pago */}
          {fatura.status === 'APPROVED' && (
            <div className="bg-white dark:bg-gray-800 rounded-xl border border-gray-200 dark:border-gray-700 p-6">
              <div className="flex items-center space-x-3 mb-4">
                <CheckCircleIcon className="w-8 h-8 text-green-500" />
                <h3 className="text-lg font-semibold text-green-600 dark:text-green-400">
                  Fatura Paga
                </h3>
              </div>
              <p className="text-gray-600 dark:text-gray-400">
                Esta fatura foi paga em {formatDateTime(fatura.dataPagamento!)}.
                Obrigado pelo pagamento!
              </p>
            </div>
          )}

          {/* Status Cancelado */}
          {fatura.status === 'CANCELLED' && (
            <div className="bg-white dark:bg-gray-800 rounded-xl border border-gray-200 dark:border-gray-700 p-6">
              <div className="flex items-center space-x-3 mb-4">
                <AlertIcon className="w-8 h-8 text-red-500" />
                <h3 className="text-lg font-semibold text-red-600 dark:text-red-400">
                  Fatura Cancelada
                </h3>
              </div>
              <p className="text-gray-600 dark:text-gray-400">
                Esta fatura foi cancelada e não pode mais ser paga.
              </p>
            </div>
          )}
        </div>

        {/* Sidebar */}
        <div className="space-y-6">
          {/* Resumo */}
          <div className="bg-white dark:bg-gray-800 rounded-xl border border-gray-200 dark:border-gray-700 p-6">
            <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-4">
              Resumo
            </h3>
            <div className="space-y-3">
              <div className="flex justify-between">
                <span className="text-gray-600 dark:text-gray-400">Status:</span>
                <span className={`font-medium ${getStatusColor(fatura.status)} px-2 py-1 rounded-full text-xs`}>
                  {fatura.status}
                </span>
              </div>
              <div className="flex justify-between">
                <span className="text-gray-600 dark:text-gray-400">Valor:</span>
                <span className="font-bold text-gray-900 dark:text-white">
                  {formatCurrency(fatura.valor)}
                </span>
              </div>
              <div className="flex justify-between">
                <span className="text-gray-600 dark:text-gray-400">Tipo:</span>
                <span className="font-medium text-gray-900 dark:text-white">
                  {fatura.type}
                </span>
              </div>
              <div className="flex justify-between">
                <span className="text-gray-600 dark:text-gray-400">Vencimento:</span>
                <span className={`font-medium ${
                  isOverdue(fatura.vencimento) && fatura.status === 'PENDING' 
                    ? 'text-red-600 dark:text-red-400' 
                    : 'text-gray-900 dark:text-white'
                }`}>
                  {formatDate(fatura.vencimento)}
                </span>
              </div>
            </div>
          </div>

          {/* Informações da Venda */}
          <div className="bg-white dark:bg-gray-800 rounded-xl border border-gray-200 dark:border-gray-700 p-6">
            <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-4">
              Venda Relacionada
            </h3>
            <div className="space-y-2">
              <p className="font-medium text-gray-900 dark:text-white">
                Venda #{fatura.vendaId}
              </p>
              <p className="text-sm text-gray-600 dark:text-gray-400">
                Cliente: {user?.nome || 'N/A'}
              </p>
            </div>
          </div>

          {/* Ações */}
          <div className="bg-white dark:bg-gray-800 rounded-xl border border-gray-200 dark:border-gray-700 p-6">
            <h3 className="text-lg font-semibold text-gray-900 dark:text-white mb-4">
              Ações
            </h3>
            <div className="space-y-2">
              <button
                onClick={() => router.push(`/dashboard/cliente/cobranca/${fatura.vendaId}`)}
                className="w-full px-4 py-2 bg-blue-600 hover:bg-blue-700 text-white rounded-lg transition-colors text-sm"
              >
                Ver Venda Relacionada
              </button>
              <button
                onClick={() => router.push('/dashboard/cliente/faturas')}
                className="w-full px-4 py-2 border border-gray-300 dark:border-gray-600 text-gray-700 dark:text-gray-300 rounded-lg hover:bg-gray-50 dark:hover:bg-gray-700 transition-colors text-sm"
              >
                Voltar para Faturas
              </button>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}
